import React from 'react'
import {Form, Button} from "react-bootstrap";
import { useState } from 'react';


export default function Footer() {
    const [dNone, setDNone] =  useState("d-none")
const showForm = ()=>{
    if(dNone==="d-none"){
setDNone("");

    } 
}
const closeForm = ()=>{
         setDNone("d-none");
 }
    return (
        <div className="foot">
            
 <Form  action="https://formspree.io/f/meqvkalg"
  method="POST" className={`newsform ${dNone}`}>
 <button type="button" onClick={closeForm} class="close" aria-label="Close">
  <span aria-hidden="true">&times;</span>
</button>
  <Form.Group controlId="formEmail">
    <Form.Label className="form-text">Email adres</Form.Label>
    <Form.Control className="form-width" type="email" placeholder="Enter email" name="_replyto" required />
  </Form.Group>

  <Form.Group controlId="formVoornaam">
    <Form.Label className="form-text">Voornaam / First name</Form.Label>
    <Form.Control className="form-width" type="text" placeholder="Voornaam" name="voornaam" required />
  </Form.Group>
  <Form.Group controlId="formAchternaam">
    <Form.Label className="form-text">Achternaam / Last name</Form.Label>
    <Form.Control className="form-width" type="text" placeholder="Achternaam" name="achternaam" required/>
  </Form.Group>
  <Form.Group controlId="formRemarks">
    <Form.Label className="form-text">Opmerkingen / remarks</Form.Label>
    <Form.Control as="textarea" rows={3} cols={18} className="form-width" type="text" placeholder="Remarks" name="opmerkingen" />
  </Form.Group>
  <div class="form-group">
      <button type="submit" class="btn btn-raised btn-warning">Send &rarr;</button>
    </div>
</Form>
<   footer>
<div className="row ml-0 mr-0"><div className="col-sm-4 signup-button"><Button variant="secondary" type="submit" onClick={showForm}>Aanmelden nieuwsbrief</Button>
            <div className="iban-text">
            <p>IBAN: NL37TRIO0320185605<br/>
                t.n.v. Stichting Aikyam</p>
                </div>
                </div>
                <div className="col-sm-4"><img src="Aikyam final white sm.png" alt=""/>

                </div>
                <div className="col-sm-4"><p><a href="Beleidsplan Stichting Aikyam mei 2021.pdf"> Beleidsplan</a></p>
                <p><a href="Privacyverklaring Aikyam.pdf"> Privacy verklaring</a></p>
                <p><a href="#"> Jaarrekening</a></p>
                </div>
                </div>
    </footer>
</div>
    )
}
